import { Component } from '@angular/core';
import {App, Events, IonicPage, NavController, NavParams, ToastController, ViewController} from 'ionic-angular';


/**
 * Generated class for the EditTelSecondPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({
  name:'edit-tel-second'
})
@Component({
  selector: 'page-edit-tel-second',
  templateUrl: 'edit-tel-second.html',
})
export class EditTelSecondPage {

  countDown:number = 1;
  timerID: number;

  tel:string ='';
  authMessage:string ='';

  constructor(public navCtrl: NavController,
              public toastCtrl: ToastController,
              public appCtrl: App,
              public events: Events,
              public viewCtrl: ViewController,
              public navParams: NavParams) {

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EditTelSecondPage');
  }
  restartSendMessage(){
    this.countDown = 60;
    this.countDownFn();
  }
  countDownFn() {
    clearInterval(this.timerID);
    this.timerID = setInterval(() => {
      if (this.countDown > 1) {
        this.countDown = this.countDown - 1;
      }
    }, 1000)
  }

  save(){
    //ajax success

    if(Math.random()>0.5){
      let toast = this.toastCtrl.create({
        message: '绑定手机失败，请重试！',
        duration: 1500,
        position:'middle',
      });
      toast.present();
    }else{
      let toast = this.toastCtrl.create({
        message: '绑定手机成功',
        duration: 1500,
        position:'middle',
      });
      toast.present();
      this.events.publish('toLogin');
    }


  }




}



